:root {
    --color-primary: #2403fc;
    --color-secondary: #3aacee;
    --color-headings: #3c1fe2;
    --color-body: #7c7896;
    --color-border: rgb(214, 210, 210);
    --border-radius: 20px;
  }
  
  *,
  *::after,
  *::before {
    box-sizing: border-box;
  }
  
  /* Typography */
  ::selection {
    background: var(--color-primary);
    color: #fff;
  }
  
  html {
    font-size: 62.5%;
  }
  
  
  body {
    font-family: Inter, Arial, Helvetica, sans-serif;
    font-size: 2rem;
    line-height: 1.5;
    color: var(--color-body);
  }
  
  h1,
  h2,
  h3 {
    color: var(--color-headings);
    margin-bottom: 1rem;
    line-height: 1.1;
  }
  
  h1 {
    font-size: 5rem;
  }
  
  h2 {
    font-size: 4rem;
  }
  
  h3 {
    font-size: 2.8rem;
    font-weight: 700;
    line-height: 1.3;
  }
  
  p {
    margin-top: 0;
  }
  
  @media screen and (min-width: 1024px) {
    body {
      font-size: 1.6rem;
    }
  
    h1 {
      font-size: 8rem;
    }
  
    h2 {
      font-size: 4rem;
    }
  
    h3 {
      font-size: 2.4rem;
    }
  }
  a {
    text-decoration: none;
    transition: color 0.3s ease-out;
  }

  a:hover {
    color: var(--color-secondary);
  }